@import './variables';
@import './mixins/mixins';
$font-size-base: 1rem;
$font-family-base: var(--ion-font-family);
.alt-font {
  font-family: $alt-font !important;
}

.custom-password {
  font-family: $pass-font !important;
}

.text-weight-medium {
  font-weight: 500 !important;
}

.text-weight-normal {
  font-weight: normal !important;
}

.text-weight-bold {
  font-weight: 600;
}

.text-small {
  font-size: rem(13);
  line-height: rem(16);
}

.text-x-small {
  font-size: rem(8);
  line-height: rem(13);
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: 0;
  letter-spacing: 0;
  font-weight: 500;
}

h5 {
  line-height: rem(24);
}

h2,
.h2 {
  line-height: rem(32) !important;
  font-size: rem(24) !important;
}

h3,
.h3 {
  line-height: rem(24) !important;
  font-size: rem(20) !important;
}

.link-small {
  font-size: rem(14);
  line-height: rem(16);
}

.h2-title {
  font-size: rem(24);
  line-height: rem(24);
}

.s13-h16 {
  font-size: rem(13) !important;
  line-height: rem(16) !important;
}

.s14-h16 {
  font-size: rem(14) !important;
  line-height: rem(16) !important;
}

.s20-h24 {
  font-size: rem(20) !important;
  line-height: rem(24) !important;
}
.s24-h32 {
  font-size: rem(24) !important;
  line-height: rem(32) !important;
}

.s32-h38 {
  font-size: rem(32) !important;
  line-height: rem(38) !important;
}

.s18-h24 {
  font-size: rem(18) !important;
  line-height: rem(24) !important;
}

.s32-h38 {
  font-size: rem(32) !important;
  line-height: rem(38) !important;
}

.s13-h15 {
  font-size: rem(13) !important;
  line-height: rem(15) !important;
}

.s13-h16 {
  font-size: rem(13) !important;
  line-height: rem(16) !important;
}

.s16-h22 {
  font-size: rem(16) !important;
  line-height: rem(22) !important;
}

.s32-h38 {
  font-size: rem(32) !important;
  line-height: rem(38) !important;
}

.s18-h21 {
  font-size: rem(18) !important;
  line-height: rem(24) !important;
}

.s24-h25 {
  font-size: rem(24);
  line-height: rem(25);
}
.s17-h16 {
  font-size: rem(17);
  line-height: rem(16);
}
.white-space-normal {
  white-space: normal;
}

.white-space-nowrap {
  white-space: nowrap;
}

.text-normal {
  font-size: rem(16);
  line-height: rem(22);
}

.text-normal-important {
  font-size: rem(16) !important;
  line-height: rem(22) !important;
  font-family: $font-family-base !important;
}

.label-normal {
  font-size: rem(14);
  line-height: rem(16);
  font-weight: normal;
}

.label-large {
  font-size: rem(20);
  line-height: rem(24);
  font-weight: 600;
}

.x-large {
  font-size: rem(32);
  line-height: rem(38);
}

.label-x-large {
  font-size: rem(24);
  line-height: rem(32);
  font-weight: 600;
}

.input-label,
ion-label.input-label,
.item .ion-label {
  font-size: rem(14) !important;
  line-height: rem(16) !important;
  transform: none !important;
  font-weight: normal;
}
.lh-16 {
  line-height: rem(16);
}
.lh-22 {
  line-height: rem(22);
}
.ls-min-02 {
  letter-spacing: -0.2px;
}

.ls-min-01 {
  letter-spacing: -0.1px;
}

.ls-02 {
  letter-spacing: -0.2px;
}
.ls-05 {
  letter-spacing: -0.5px;
}

.ls-05 {
  letter-spacing: -0.5px;
}

.ls-01 {
  letter-spacing: -0.1px;
}

.ls-0 {
  letter-spacing: 0;
}

.border-danger {
  padding-bottom: rem(7);
  border-bottom: rem(1) solid $red;
}
.border-green {
  padding-bottom: rem(7);
  border-bottom: rem(1) solid $green;
}

.bottom-border {
  border-bottom: rem(1) solid $red;
}

.text-nowrap {
  word-wrap: nowrap;
}

.overflow-ellipsis {
  overflow: hidden;
  text-overflow: ellipsis;
}

.remove-event {
  pointer-events: none;
}

.certificat {
  height: rem(120) !important;
  span {
    ion-icon {
      margin-right: 0 !important;
      font-size: rem(130) !important;
    }
  }
}

.atentie {
  font-size: rem(24) !important;
  font-weight: 600;
  color: var(--app-red-color) !important;
}


.font-reset {
  font: inherit !important;
}

.shadow-1 {
  box-shadow: $box-shadow-1;
}
.shadow-3 {
  box-shadow: $box-shadow-3;
}

.shadow-3 {
  box-shadow: $box-shadow-3;
}

.font-128 {
  font-size: rem(128);
}

.z-index-999 {
  z-index: 999;
}

.text-center {
  text-align: center;
}

a.no-decoration {
  text-decoration: none !important;
}
.is-not-visible {
  visibility: hidden !important;
}